@import url(//fonts.googleapis.com/css?family=IBM Plex Sans Condensed);
code {
  font-family: 'IBM Plex Sans Condensed', source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.about-column{height:100%;width:100%;padding:20px 10px 0px 10px;display:flex;flex-direction:column;align-items:center;justify-content:space-around;line-height:35px}.social-row{width:200px;padding:10px;display:flex;flex-direction:row;justify-content:space-between}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


html,
body,
ul {
  padding: 0;
  margin: 0;
  width: 100%;
  background-color: #DFE4FB;
  height: 100vh;
  font-family: 'IBM Plex Sans Condensed', sans-serif;
  max-width: 100%;
  overflow-x: hidden;
}
